<template>
  <div>
    <p>输入学号查询</p>
    <input type="text" v-model="studentId" placeholder="请输入学号">
    <input type="button" value="查询" @click="find">
  </div>
</template>

<script>
export default {
  name: '',
  data: function () {
    return {
      studentId: ''
    }
  },
  methods: {
    find () {
      this.$axios.get('http://localhost:8080/hadChoose', {
        params: {
          studentId: this.studentId
        }
      })
    }
  }
}

</script>

<style>

</style>



