<template>
  <div class="bgPicture" :style="bgPicture">
    <div id="StudentLogin">
      <h1>StudentLogin</h1>
      <br>
      <br>
      <br>
      <br>
      <br>
      <br>
      <br>
      <el-form ref = "Account" v-model ="account"
               label-width="100px"
               rules = "rules"
               class = "demo-ruleForm login-container">
        <el-form-item>
          <p>username:</p>
          <input type="text"
                 class = "form-control"
                 label = "username"
                 prop="username"
                 v-model="studentUsername"
                 auto-complete = "on"
                 placeholder = "请输入账号">
          <br>
        </el-form-item>
        <el-form-item>
          <p>password:</p>
          <input type="password"
                 class = "form-control"
                 label = "password"
                 prop="password"
                 v-model="studentPwd"
                 auto-complete = "off"
                 placeholder = "请输入密码">
          <br>
          <br>
        </el-form-item>
        <el-form-item>
          <input type="button" value="登录" class="button" @click="LoginForStudent">
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'studentLogin',
  data () {
    return {
      studentUsername: '',
      studentPwd: '',
      '/studentLogin': 'StudentLogin',
      bgPicture: {
        position: 'absolute',
        top: '0px',
        left: '0px',
        width: '100%',
        height: '100%',
        backgroundImage: 'url(' + require('/Users/xzy/Desktop/courseSystem-vue/src/assets/img4.jpg') + ')',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat'
      },
      account: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    LoginForStudent: function () {
      this.$axios.get('http://localhost:8080/student?id=' + this.studentUsername)
        .then((res) => {
          if (res.data.studentPassword === this.studentPwd) {
            this.$router.push({
              path: 'StudentPage'
            })
          } else {
            alert('请输入正确的用户名和密码')
          }
        })
      this.$store.state.studentUsername2 = this.studentUsername
    }
  }
}

</script>

<style scoped>

  .demo-ruleForm login-container {
    width:300px;
    height: 50px;
    background-color: #f24b48;
    margin-left: 42.5%;
  }
  h1{
    color: #ffffff;
    font-size: 100px;
  }
  .form-control{
    width: 300px;
    height: 50px;
  }
  p{
    color: white;
    font-size: 30px;
  }
  .button{
    width: 100px;
    height: 40px;
    font-size: 20px;
  }
</style>



