<template>
  <div class="bgPicture" :style="bgPicture">
    <div id="TeacherLogin">
      <h1>TeacherLogin</h1>
      <br>
      <br>
      <br>
      <br>
      <br>
      <br>
      <br>
      <el-form ref = "Account" v-model ="account"
               label-width="100px"
               rules = "rules"
               class = "demo-ruleForm login-container">
        <el-form-item>
          <p>username:</p>
          <input type="text"
                 class = "form-control"
                 label = "username"
                 prop="username"
                 v-model="teacherUsername"
                 auto-complete = "on"
                 placeholder = "请输入账号">
          <br>
        </el-form-item>
        <el-form-item>
          <p>password:</p>
          <input type="password"
                 class = "form-control"
                 label = "password"
                 prop="password"
                 v-model="teacherPwd"
                 auto-complete = "off"
                 placeholder = "请输入密码">
          <br>
          <br>
        </el-form-item>
        <el-form-item>
          <button type="primary"
                  @click="LoginForTeacher">sign in</button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'teacherLogin',
  data () {
    return {
      '/teacherLogin': 'TeacherLogin',
      bgPicture: {
        position: 'absolute',
        top: '0px',
        left: '0px',
        width: '100%',
        height: '100%',
        backgroundImage: 'url(' + require('/Users/xzy/Desktop/courseSystem-vue/src/assets/img1.jpg') + ')',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat'
      },
      teacherUsername: '',
      teacherPwd: '',
      rules: {
        account_teacherUsername: [
          {
            require: true, message: '请输入账号', trigger: 'blur'
          }
        ],
        account_teacherPwd:
            [
              {
                require: true, message: '请输入密码', trigger: 'blur'
              }
            ]
      }
    }
  },
  methods: {
    LoginForTeacher: function () {
      this.$axios.get('http://localhost:8080/teacher?id=' + this.teacherUsername)
        .then((res) => {
          if (res.data.teacherPassword === this.teacherPwd) {
            this.$router.push({
              path: 'TeacherPage'
            })
          } else {
            alert('请输入正确的用户名和密码')
          }
        })
    }
  }
}
</script>

<style scoped>

  .demo-ruleForm login-container {
    width:300px;
    height: 50px;
    background-color: #f24b48;
    margin-left: 42.5%;
  }
  h1{
    color: #ffffff;
    font-size: 100px;
  }
  .form-control{
    width: 300px;
    height: 50px;
  }
  p{
    color: white;
    font-size: 30px;
  }
  button{
    width: 100px;
    height: 40px;
    font-size: 20px;
  }
</style>



