<template>
  <div class="bgPicture" :style="bgPicture">
    <div class="Hello">
      <div class="panel panel-primary">
        <div class="panel-heading">
          <h3 class="panel-title" >welcome to CourseSystem</h3>
        </div>
        <div class="panel-body form-inline">
          <label>选课编号:<input type="text"
                             class="form-control"
                             v-model="id"></label>
          <label>选课名称:<input type="text"
                             class="form-control"
                             v-model="name"></label>
          <label>任课教师:<input type="text"
                             class="form-control"
                             v-model="teacher"></label>
          <label>所属院系:<input type="text"
                             class="form-control"
                             v-model="department"></label><br>
          <label>选课学分:<input type="text" class="form-control" v-model="credit"></label>
          <label>开课时间:<input type="text" class="form-control" v-model="time"></label>
          <label>开课地点:<input type="text" class="form-control" v-model="location"></label>
          <label>最大人数:<input type="text" class="form-control" v-model="mixNumber"></label><br>
          <button type="primary"
                  @click="add">发布</button>
        </div>
      </div>
      <table class="table table-border table-hover table-striped">
        <thead>
        <tr>
          <th>选课编号</th>
          <th>选课名称</th>
          <th>任课教师</th>
          <th>所属院系</th>
          <th>选课学分</th>
          <th>开课时间</th>
          <th>开课地点</th>
          <th>最大人数</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <!--在search方法内部执行for循环，把所有符合搜索关键字的数据，保存到一个新数组中返回-->
        <tr v-for="(item, index) in list" :key="index.id">
          <th>{{ item.id }}</th>
          <th>{{ item.courseName }}</th>
          <th>{{ item.courseTeacher }}</th>
          <th>{{ item.courseDepartment }}</th>
          <th>{{ item.courseCredit }}</th>
          <th>{{ item.courseTime }}</th>
          <th>{{ item.courseLocation }}</th>
          <th>{{ item.courseMixnumber }}</th>
          <th><a href="" @click.prevent="del(item.id)">delete</a></th>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      id: '',
      name: '',
      teacher: '',
      department: '',
      credit: '',
      time: '',
      location: '',
      mixNumber: '',
      keywords: '',
      list: [
      ],
      bgPicture: {
        position: 'absolute',
        top: '0px',
        left: '0px',
        width: '100%',
        height: '100%',
        backgroundImage: 'url(' + require('/Users/xzy/Desktop/courseSystem-vue/src/assets/img5.jpeg') + ')',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat'
      }
    }
  },
  created () {
    this.$axios.get('http://localhost:8080/course/all/1')
      .then((res) => {
        this.list = res.data.data
        console.log(res.data.data)
      })
  },
  methods: {
    // 新增方法
    add: function () {
      this.$axios.post('http://localhost:8080/course', {
        id: this.id,
        courseName: this.name,
        courseDepartment: this.department,
        courseTime: this.time,
        courseLocation: this.location,
        courseCredit: this.credit,
        courseMixnumber: this.mixNumber,
        courseTeacher: this.teacher
      })
        .then(function (response) {
          console.log(response)
        })
        .catch(function (error) {
          console.log(error)
        })
      window.alert('发布课程成功')
    },
    // 删除方法
    del: function (id) {
      console.log(id)
      this.$axios.delete(`http://localhost:8080/course/${id}`)
        .then((res) => {
          console.log(res)
        })
      window.alert('课程删除成功')
    }
  }
}
</script>

<style scoped>
  table,tr,td {
    border: 3px solid #7b6dba;
    width: 100%;
    height: 50px;
    background-color: aliceblue;
    background-color: rgba(211, 252, 201, 0.5);
  }
  h3 {
    font-size: 90px;
    color: #fffdea;
  }
  button{
    width: 50px;
    height: 30px;
    font-size: 15px;
  }
</style>



