<template>
  <div class="bgPicture" :style="bgPicture">
    <div class="Hello">
      <div class="panel panel-primary">
        <div class="panel-heading">
          <h3 class="panel-title" >welcome to CourseSystem</h3>
          <p class="p2">欢迎，{{ studentUsername2 }}</p>
        </div>
      </div>
      <table class="table table-border table-hover table-striped">
        <thead>
        <tr>
          <th>选课编号</th>
          <th>选课名称</th>
          <th>任课教师</th>
          <th>所属院系</th>
          <th>选课学分</th>
          <th>开课时间</th>
          <th>开课地点</th>
          <th>已选人数</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, index) in list" :key="index">
          <th>{{ item.id }}</th>
          <th>{{ item.courseName }}</th>
          <th>{{ item.courseTeacher }}</th>
          <th>{{ item.courseDepartment }}</th>
          <th>{{ item.courseCredit }}</th>
          <th>{{ item.courseTime }}</th>
          <th>{{ item.courseLocation }}</th>
          <th>{{ item.courseMixnumber }}</th>
          <th><a href="" @click.prevent="selectCourse(item)">选择</a></th>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HelloWorld',
  data () {
    return {
      studentUsername: '',
      studentName: '',
      list: [],
      bgPicture: {
        position: 'absolute',
        top: '0px',
        left: '0px',
        width: '100%',
        height: '100%',
        backgroundImage: 'url(' + require('/Users/xzy/Desktop/courseSystem-vue/src/assets/img5.jpeg') + ')',
        backgroundSize: 'cover',
        backgroundRepeat: 'no-repeat'
      }
    }
  },
  created () {
    this.$axios.get('http://localhost:8080/course/all/1')
      .then((res) => {
        this.list = res.data.data
        console.log(res.data.data)
      })
  },
  computed: {
    studentUsername2 () {
      return this.$store.state.studentUsername2
    }
  },
  methods: {
    selectCourse: function (item) {
      this.$axios.post('http://localhost:8080/hadChoose', {
        id: 0,
        course: item.courseName,
        teacher: item.courseTeacher,
        student: this.$store.state.studentUsername2,
        isPass: 0
      })
        .then(function (response) {
          console.log(response)
        })
        .catch(function (error) {
          console.log(error)
        })
      window.alert('选择课程成功')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  table,tr,td {
    border: 3px solid #7b6dba;
    width: 100%;
    height: 50px;
    background-color: wheat;
    background-color: rgba(219, 227, 224, 0.5);
  }
  h3 {
    font-size: 90px;
    color: #354b64;
  }
  .p2 {
    text-align: -webkit-left;
    font-size: 25px;
    text-indent: 2em;
  }
</style>



